<script>
import Modal from '@components/Modal/Modal.vue';
import { actions as dictionatiesActions } from '@/store/dictionaries/constants';

import { createNamespacedHelpers } from 'vuex';
import {
  getters as contactsGetters,
  mutations as contactsMutations,
  actions as contactsActions,
} from '@modules/contacts/constants';

const {
  mapActions: mapDictionatiesActions,
  mapState: mapDictionatiesState,
} = createNamespacedHelpers('dictionaries');

const {
  mapGetters: mapContactsGetters,
  mapState: mapContactsState,
  mapActions: mapContactsActions,
  mapMutations: mapContactsMutations,
} = createNamespacedHelpers('contacts');

export default {
  name: 'Contact',
  components: { Modal },
  props: {
    id: {
      type: [String, Number],
      required: true,
    },
  },
  data: () => ({
    modalOpened: false,
    deleting: false,
  }),
  methods: {
    ...mapContactsMutations({
      clearContactsListDelete: contactsMutations.CLEAR_CONTACTS_LIST_DELETE,
    }),
    ...mapContactsActions({
      deleteContacts: contactsActions.DELETE_CONTACTS,
      deleteContact: contactsActions.DELETE_CONTACT,
      getContacts: contactsActions.GET_CONTACTS,
    }),
    handleDelete() {
      this.deleteContact({ id: this.id })
        .then(() => {
          this.$notificationSuccess('The contact was deleted');
          this.getContacts()
            .then((data) => {
              if (window.innerWidth >= 1264) {
                this.$router.push({ name: this.$constants.ROUTE_NAMES.CONTACT, params: { id: data[0].id } }).catch(() => {});
              } else {
                this.$router.push({ name: this.$constants.ROUTE_NAMES.MOBILE_CONTACT, params: { id: data[0].id } }).catch(() => {});
              }
            });
        })
        .catch(() => {
          this.$notificationError('The contact wasn\'t deleted');
        })
        .finally(() => {
          this.modalOpened = false;
        });
    },
    ...mapDictionatiesActions({
      getStates: dictionatiesActions.GET_STATES,
    }),
  },
  computed: {
    ...mapContactsGetters({
      contactsListDelete: contactsGetters.CONTACTS_LIST_DELETE,
    }),
    ...mapContactsState(['contacts']),
    ...mapDictionatiesState(['states']),
    contact() {
      return this.contacts.data.find(({ id }) => +id === +this.id) || {};
    },
  },
  mounted() {
    this.getStates();
  },
};
</script>

